@import "nullstyle.scss";

body {
  margin: 0;
  font-family: "Inter", system-ui;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #e7f6ff;
}

.wrapper {
  background-color: #ffffff;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
  border-radius: 20px;
  max-width: 1080px;
  margin: 85px auto;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 45px;
  border-bottom: 1px solid #eaeaea;

  .headerLeft {
    display: flex;
    align-items: center;

    .headerLeft__block p {
      opacity: 0.5;
    }

    img {
      margin-right: 15px;
    }

    h3 {
      text-transform: uppercase;
    }
  }

  .headerRight {
    display: flex;

    li:first-child {
      cursor: pointer;
      margin-right: 30px;
    }

    .cart {
      margin-right: 10px;
    }

    .heart {
      margin-right: 30px;
      cursor: pointer;
    }
  }
}

.content {
  padding: 45px;

  .content__body {
    display: flex;
    margin-bottom: 40px;
    align-items: center;
    justify-content: space-between;
  }

  h1 {
    font-weight: 700;
    font-size: 32px;
  }

  .body__search {
    position: relative;
    display: flex;
    border: 1px solid #f3f3f3;
    border-radius: 10px;
    padding: 0 15px;

    input {
      border: 0;
      padding: 13px;
      font-size: 16px;
      width: 200px;
    }

    .clear {
      position: absolute;
      right: 10px;
      top: 7px;
      cursor: pointer;
    }
  }

  .content__row {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -20px;
  }
}

.cartItem {
  display: flex;
  align-items: center;
  border: 1px solid #f3f3f3;
  // box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 20px;
  overflow: hidden;
  padding: 20px 20px 30px 20px;
  margin-bottom: 20px;

  .cartItem__image {
    margin-right: 20px;
  }

  .cartItem__block {
    margin-right: 12px;

    p {
      margin-bottom: 8px;
    }
    b {
      font-weight: 700;
    }
  }
  .removeBtn {
    opacity: 0.5;
    cursor: pointer;
    transition: opacity 0.15s ease-in-out;

    &:hover {
      opacity: 1;
    }
  }
}

.cartTotalBlock {
  ul {
    margin-bottom: 25px;
    li {
      display: flex;
      align-items: flex-end;
      margin-bottom: 20px;

      div {
        flex: 1;
        height: 1px;
        border-bottom: 1px dashed #dfdfdf;
        margin: 0 7px;
      }
    }

    li:last-child {
      margin-bottom: 0;
    }
  }

  .greenButton {
    position: relative;

    &:disabled {
      animation: button-loading 0.6s ease-in-out infinite;
    }

    &:hover {
      img {
        transform: translateX(5px);
      }
    }
    img {
      position: absolute;
      right: 30px;
      top: 20px;
      transition: transform 0.2s ease-in-out;
    }
  }
}

.empty {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto 0;

  .box {
    margin-bottom: 20px;
  }
  .title {
    font-weight: 600;
    font-size: 22px;
    line-height: 27px;
    margin-bottom: 10px;
  }
  .text {
    font-size: 16px;
    line-height: 24px;
    opacity: 0.4;
    text-align: center;
    margin-bottom: 40px;
  }
  .return {
    position: relative;
    &:hover {
      img {
        transform: translateX(5px);
      }
    }
    img {
      position: absolute;
      left: 30px;
      top: 20px;
      transition: transform 0.2s ease-in-out;
    }
  }
}

.greenButton {
  width: 100%;
  height: 55px;
  background: #9dd558;
  border-radius: 18px;
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.1 ease-in-out;

  &:disabled {
    background-color: #bebebe !important;
    cursor: default;
  }

  &:hover {
    background: lighten(#9dd558, 5%);
  }
  &:active {
    background: darken(#9dd558, 5%);
  }
}

@keyframes button-loading {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
  100% {
    opacity: 1;
  }
}
